import { CommonModule } from '@angular/common';
import { Component, DestroyRef, inject, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, AsyncValidatorFn, FormControl, FormGroup, FormsModule, ReactiveFormsModule, ValidationErrors } from '@angular/forms';
import {
  ForgeButtonModule,
  ForgeCheckboxModule,
  ForgeCircularProgressModule,
  ForgeIconButtonModule,
  ForgeIconModule,
  ForgeTextFieldModule,
  ForgeToolbarModule,
  ToastService
} from '@tylertech/forge-angular';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { finalize, of, Subject } from 'rxjs';
import { fileTypeFromStream } from 'file-type';

import { Utils } from 'src/utils';
import { AppDataService } from 'src/app/app-data.service';
import { BusyIndicatorService, CardComponent } from 'src/app/shared/components';
import { ArrayFindPipe } from 'src/app/shared/pipes';
import { AppWebSocketService } from 'src/app/app-web-socket.service';
import { AppCacheService } from 'src/app/app-cache.service';

@Component({
  selector: 'app-examples-misc',
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    ForgeButtonModule,
    ForgeCheckboxModule,
    ForgeCircularProgressModule,
    ForgeIconButtonModule,
    ForgeIconModule,
    ForgeTextFieldModule,
    ForgeToolbarModule,
    CardComponent,
    ArrayFindPipe
  ],
  templateUrl: './misc.component.html',
  styleUrls: ['./misc.component.scss']
})
export class MiscComponent implements OnInit, OnDestroy {
  private destroyRef = inject(DestroyRef);
  private appCacheService = inject(AppCacheService);
  private appDataService = inject(AppDataService);
  private appWebSocketService = inject(AppWebSocketService);
  private busyIndicatorService = inject(BusyIndicatorService);
  private toastService = inject(ToastService);
  private propertyPathsData = {
    firstName: 'First',
    lastName: 'Last',
    age: 12,
    citizen: false,
    address: {
      city: 'ctiy value',
      state: 'state value',
      districts: [
        {
          name: 'district 1',
          location: 'district 1 location',
          pets: ['rock', 'dog', 'cat']
        },
        { name: 'district 2', location: 'district 2 location' },
        { name: 'district 3', location: 'district 3 location' }
      ]
    },
    friends: ['Homer', 'Marge', 'Bart', 'Lisa', 'Maggie']
  };
  private asyncValidators = new Map<
    string,
    {
      value: any;
      control: AbstractControl;
      response: Subject<ValidationErrors | null>;
    }
  >();

  public arrayData = [
    { value: 0, label: 'Item 0' },
    { value: 1, label: 'Item 1' },
    { value: 2, label: 'Item 2' },
    { value: 3, label: 'Item 3' },
    { value: 4, label: 'Item 4' },
    { value: 5, label: 'Item 5' },
    { value: 6, label: 'Item 6' },
    { value: 7, label: 'Item 7' },
    { value: 8, label: 'Item 8' },
    { value: 9, label: 'Item 9' }
  ];
  public propertyPaths: object;
  public fileName = '';
  public fileEncoded = false;
  public openAsDownload = false;
  public formGroup = new FormGroup({
    name: new FormControl('', {
      asyncValidators: [this.asyncValidator('name')]
    }),
    age: new FormControl('', { asyncValidators: [this.asyncValidator('age')] }),
    location: new FormControl('', {
      asyncValidators: [this.asyncValidator('location')]
    })
  });
  public isRequesting = false;
  public isPolling = false;
  public isMessaging = false;
  public response: string;

  public ngOnInit() {
    this.propertyPaths = Utils.objectPropertyPaths(this.propertyPathsData);
    this.appWebSocketService.webSocketSubject.pipe(takeUntilDestroyed(this.destroyRef)).subscribe({
      next: (result) => {
        this.isMessaging = false;
        console.log(result);
        this.response = JSON.stringify(result);
        this.appCacheService.isBusy.set(false);
      }
    });
  }

  public ngOnDestroy(): void {
    this.appWebSocketService.webSocketSubject.complete();
  }

  public onShowFile() {
    this.appDataService
      .getFile(this.fileName)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (result) => {
          if (result?.size && result.type !== 'text/html') {
            if (this.fileEncoded) {
              result.text().then((value) => {
                const encodedBlob = this.base64toBlob(value);
                if (encodedBlob) {
                  this.openFile(encodedBlob);
                }
              });
            } else {
              this.openFile(result);
            }
          } else {
            this.toastService.show({
              message: 'An error occurred downloading the file',
              duration: Infinity,
              dismissible: true
            });
          }
        },
        error: () => {
          this.toastService.show({
            message: 'An error occurred downloading the file',
            duration: Infinity,
            dismissible: true
          });
        }
      });
  }

  public onShowBusy() {
    const config = {
      message: 'Processing request...',
      progress: 'circular' as const,
      title: 'Processing title'
    };
    this.busyIndicatorService.show(config);
    window.setTimeout(() => {
      this.busyIndicatorService.hide();
    }, 5000);
  }

  public onValidate() {
    if (this.asyncValidators.size) {
      const key = this.asyncValidators.keys().next().value;
      const validator = this.asyncValidators.get(key);
      // validator.response.next({ wrongFormat: true });
      validator.response.next(null);
      validator.response.complete();
      // validator.control.markAsPristine();
      // validator.control.markAsUntouched();
      this.asyncValidators.delete(key);
    }
  }

  public onResetForm() {
    this.asyncValidators.forEach((v) => {
      v.response.next(null);
      v.response.complete();
    });
    this.asyncValidators.clear();
    this.formGroup.reset();
  }

  private asyncValidator(name: string): AsyncValidatorFn {
    return (control: AbstractControl) => {
      if (!control.dirty) {
        return of(null);
      }

      if (!this.asyncValidators.has(name)) {
        this.asyncValidators.set(name, {
          value: control.value,
          control,
          response: new Subject<ValidationErrors | null>()
        });
      }

      return this.asyncValidators.get(name).response;
    };
  }

  private base64toBlob(value: string): Blob {
    // powershell script to convert to base64
    // $fileBytes = [System.IO.File]::ReadAllBytes(file)
    // $encodedText = [Convert]::ToBase64String($fileBytes)
    // [System.IO.File]::WriteAllText(file-output, $encodedText)

    let binaryValue: string;
    try {
      binaryValue = atob(value);
    } catch (e) {
      console.error(e);
      this.toastService.show({
        message: 'An error occurred decoding the base64 file',
        duration: Infinity,
        dismissible: true
      });
      return undefined;
    }
    const valueArray = Uint8Array.from(binaryValue, (c) => c.charCodeAt(0));
    return new Blob([valueArray]);
  }

  private openFile(value: Blob) {
    fileTypeFromStream(value.stream()).then((fileTypeResult) => {
      const resultBlob = new Blob([value], {
        type: fileTypeResult?.mime?.length ? fileTypeResult.mime : 'application/octet-stream'
      });
      const resultBlobUrl = URL.createObjectURL(resultBlob);

      if (this.openAsDownload) {
        let openFileName = this.fileName;
        if (openFileName.split('.').at(-1) !== fileTypeResult.ext) {
          openFileName += `.${fileTypeResult.ext}`;
        }

        const linkElement = document.createElement('a') as HTMLAnchorElement;
        linkElement.style.display = 'none';
        linkElement.href = resultBlobUrl;
        linkElement.setAttribute('download', openFileName);
        document.body.appendChild(linkElement);
        linkElement.click();
        linkElement.remove();
      } else {
        window.open(resultBlobUrl);
      }

      URL.revokeObjectURL(resultBlobUrl);
    });
  }

  public onMakeLongRequest() {
    this.isRequesting = true;
    this.response = undefined;
    this.appDataService
      .getLongRequest()
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => (this.isRequesting = false))
      )
      .subscribe({
        next: (result) => {
          console.log(result);
          this.response = JSON.stringify(result);
        },
        error: (error) => console.log(error)
      });
  }

  public onMakePollingRequest() {
    this.isPolling = true;
    this.response = undefined;
    this.appDataService
      .pollingRequest(10)
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        finalize(() => (this.isPolling = false))
      )
      .subscribe({
        next: (r) => {
          console.log(r);
          this.response = JSON.stringify(r);
        }
      });
  }

  public onSendMessage() {
    this.isMessaging = true;
    this.response = undefined;
    this.appCacheService.isBusy.set(true);
    this.appWebSocketService.webSocketSubject.next({
      data: 'message from front end.',
      date: new Date()
    });
  }
}
